import React, { useEffect, useState } from 'react';
import { Button, Input } from 'reactstrap';

export const Rep = ({ item }) => {

  useEffect(() => {
  }, [editable, , ]);

  const handleOnclickEdit = e => {
    setIsEditable(true);
  };

  const handleOnclickDone = e => {
    setIsEditable(false);
  };

  return (
    <tr>
      <th scope="row">{id}</th>
      <td>
        {isEditable ? (
          <Input type="number" value={_numReps} onChange={event => setNum(event.target.value)} />
        ) : (
          _numReps
        )}
      </td>
      <td>
        {isEditable ? (
          <Input type="number" value={_weigth} onChange={event => setWeigth(event.target.value)} />
        ) : (
          _weigth
        )}
      </td>
      <td>
        <Button
          color={!isEditable ? 'primary' : 'danger'}
          onClick={!isEditable ? handleOnclickEdit : handleOnclickDone}
        >
          {!isEditable ? 'edit' : 'done'}
        </Button>
      </td>
    </tr>
  );
};
